<template>
  <user>
    <html-head 
      title="Trashed Roles"
    ></html-head>

    <page-header>
      <template v-slot:title>Trashed Roles</template>
    </page-header>

    <template v-if="resources">
      <crud-table-card 
        :api="api" 
        :headers="headers" 
        :optionsChanged="optionsChanged" 
        :resources="resources" 
        name="role"
        :deletePermanently="true"
        :restore="true"
      ></crud-table-card>
    </template>
  </user>
</template>

<script>
import Api from './routes/api'
import Resources from '@/core/models/Resources'

export default {
  data: () => ({
    api: Api,
    headers: [
      {
        text: 'Name',
        align: 'start',
        value: 'name',
      },
      {
        text: 'Created',
        align: 'start',
        value: 'createdAt',
      },
      {
        text: 'Actions',
        align: 'end',
        value: 'actions',
      },
    ],

    resources: new Resources
  }),

  methods: {
    optionsChanged ( options ) {
      this.resources.optionsChanged(
        {
          method: 'get',
          url: Api.trashed(),
          params: {
            search: this.resources.search,
            searchFields: "firstName,lastName",
            options: this.resources.options
          }
        },
        options 
      )
    },
  },

  mounted () {
    this.resources.load( false )
  }
}
</script>